body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

form.form {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

label {
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 5px;
  padding: 5px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 20px;
}

button[type="submit"]:hover {
  background-color: #3e8e41;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

